var phoneNumber, password, verifyCode, that;
export default {
    data() {
        return {
            state: false, //是否开启倒计时
            totalTime: 60, //总时间，单位秒
            recordingTime: 0, //记录时间变量
            currentTime: 0, //显示时间变量
            modalHidden: true, //模态框隐藏
            verifySrc: "" ,//验证码图片地址
            modalShow: null,
        }
    },
    onLoad() {
        that = this;
    },
    methods: {
        showModal: function(e) {
            this.modalShow = e.currentTarget.dataset.target;
            this.checking();
        },
        hideModal: function (e) {
        	this.modalShow = null
        },
        checking: function() {
            this.getRandomCode();
            //把显示时间设为总时间
            this.currentTime = this.totalTime;
            //开始倒计时
            this.state = true;
            //执行倒计时
            this.checkingTime();
        },
        checkingTime: function() {
            //判断是否开启
            if (this.state) {
                //判断显示时间是否已到0，判断记录时间是否已到总时间
                if (this.currentTime > 0 && this.recordingTime <= this.totalTime) {
                    //记录时间增加 1
                    this.recordingTime = this.recordingTime + 1;
                    //显示时间，用总时间 - 记录时间
                    this.currentTime = this.totalTime - this.recordingTime;
                    //1秒钟后，再次执行本方法
                    setTimeout(() => {
                        //定时器内，this指向外部，找不到vue的方法，所以，需要用that变量。
                        that.checkingTime();
                    }, 1000)
                } else {
                    //时间已完成，还原相关变量
                    this.modalHidden = true;;
                    this.state = false; //关闭倒计时
                    this.recordingTime = 0; //记录时间为0
                    this.currentTime = this.totalTime; //显示时间为总时间
                }
            } else {
                //倒计时未开启，初始化默认变量
                this.state = false;
                this.recordingTime = 0;
                this.currentTime = this.totalTime;
            }
        },
        getRandomCode: function() { // 生成验证码
            let code = new Array();
            let codeLength = 6; //验证码的长度               
            let selectChar = new Array(2, 3, 4, 5, 6, 7, 8, 9, 'a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'j', 'k',
                'l', 'm', 'n', 'p', 'q', 'r', 's', 't', 'u', 'v', 'w', 'x', 'y', 'z', 'A', 'B', 'C', 'D',
                'E', 'F', 'G', 'H', 'J', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'U', 'V', 'W', 'X',
                'Y', 'Z');
            for (let i = 0; i < codeLength; i++) {
                let charIndex = Math.floor(Math.random() * 56); // 56是selectChar数组的长度
                code += selectChar[charIndex];
                verifyCode = code;
            }
            if (code.length != codeLength) {
                this.getRandomCode();
            }                              
            uni.request({  
                url: that.apiServer + 'user&m=verifyView',
                method: 'POST',
                header: {
                    'content-type': 'application/x-www-form-urlencoded'
                },
                data: {
                    code
                },
                success: (res) => {
                    res = res.data;
                    let verifyImg = 'url(' + res.data +')';
                    this.verifySrc = verifyImg;
                }  
            });  
        },
        formSubmit: function(e) { // 检验表单数据
            phoneNumber = e.detail.value.phoneNumber;
            password = e.detail.value.password;
            let inputCode = e.detail.value.verifyCode;
            // 正则表达式验证
            let phoneNumberReg = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/;
            let passwordReg = /^(\w){8,16}$/;
            let inputCodeReg = /^[A-Za-z0-9]{6,6}$/;

            if (phoneNumber == '') {
                uni.showToast({
                    title: '手机号不能为空',
                    duration: 1500,
                    icon: 'none'
                });
                return false;
            } else if (!phoneNumberReg.test(phoneNumber)) {
                uni.showToast({
                    title: '手机号格式不正确',
                    duration: 1500,
                    icon: 'none'
                });
                return false;
            } else if (inputCode == '') {
                uni.showToast({
                    title: '验证码不能为空',
                    duration: 1500,
                    icon: 'none'
                });
                return false;
            } else if (!inputCodeReg.test(inputCode)) {
                uni.showToast({
                    title: '请输入6位验证码',
                    duration: 1500,
                    icon: 'none'
                });
                return false;
            } else if (!verifyCode) {
                uni.showToast({
                    title: '请先获取验证码',
                    duration: 1500,
                    icon: 'none'
                });
                return false;
            } else if (inputCode.toLowerCase() !== verifyCode.toLowerCase()) {
                uni.showToast({
                    title: '验证码不正确',
                    duration: 1500,
                    icon: 'none'
                });
                return false;
            } else if (password == '') {
                uni.showToast({
                    title: '密码不能为空',
                    duration: 1500,
                    icon: 'none'
                });
                return false;
            } else if (!passwordReg.test(password)) {
                uni.showToast({
                    title: '密码长度6~20且只能由字母、数字、下划线组成',
                    duration: 1500,
                    icon: 'none'
                });
                return false;
            } else {
                uni.showLoading();
                uni.request({
                    url: that.apiServer + 'user&m=register',
                    method: 'POST',
                    header: {
                        'content-type': 'application/x-www-form-urlencoded'
                    },
                    data: {
                        phoneNumber,
                        password,
                    },
                    // 请求成功执行的代码
                    success: res => {
                        res = res.data;
                        if (res.status == "error") {
                            uni.hideLoading();
                            uni.showToast({
                                title:res.data,
                                image: '../../static/images/inc/icon_warning.png',
                                duration: 2000
                            })
                            return false;
                        } else if (res.status == "ok") {
                            uni.hideLoading();
                            uni.showToast({
                                title:res.data,
                                image: '../../static/images/inc/icon_success.png',
                                duration: 2000
                            });
                            setTimeout(function() {
                                uni.reLaunch({
                                url:'../login/login',
                                })
                            },2500);
                        }
                    },
                });
            }
        },
    }
}
